<script>
// import jsonTimetable from "@/assets/json/timetable.json"

import { getCurrentInstance } from 'vue';
import BusSchedule from './BusSchedule.vue';
import KitchenCar from "./KitchenCar.vue";

const usevuetify = () => {
    const instance = getCurrentInstance()
    if (!instance) {
        throw new Error(`vuetify instance doesn't exist`)
    }
    return instance.proxy.$vuetify
}

export default {
    name: 'WelcomePage',
    data: () => ({
        Month: "",
        Day: "",
        clock: "00:00:00",
        UntillBusYkusa: "",
        UntillBusAit: "",
        UntilClass: "",
        Now_yakusa: "",
        Now_ait: "",
        TimeTables: [],
        Now_lecture: null,
        weeks: [
            '',
            '',
            '',
            '',
            '',
        ],
        week: 0,
        AikodaiId: "",
        CarFood: [],

    }),
    components: {
        BusSchedule,
        KitchenCar,
    },
    props: {
        // 
        nowYakusa: {
        },
        nowAit: {

        },
        aikodaiId: {
            type: String,
            default: '',
        },
        timeTable: {

        },
    },
    methods: {
        period2time: function (period) {
            if (period == 1) {
                return "9:00"
            } else if (period == 2) {
                return "10:40"
            } else if (period == 3) {
                return "13:00"
            } else if (period == 4) {
                return "14:40"
            } else if (period == 5) {
                return "16:20"
            } else if (period == 6) {
                return "18:00"
            } else {
                return ""
            }
        },
        tick: function () {// 
            // console.log("tick!!");

            const date = new Date();  // Date
            this.Month = date.getMonth() + 1;  // 0+1
            this.Day = date.getDate();
            let h = date.getHours();  // 
            let m = date.getMinutes();// 
            let s = date.getSeconds();// 

            if (h < 10) h = "0" + h;   // 100
            if (m < 10) m = "0" + m;
            if (s < 10) s = "0" + s;
            this.clock = h + ":" + m + ":" + s;// 
            // 
            if (this.Now_yakusa != "" || this.Now_yakusa != '') {
                const yakusa = this.Now_yakusa.split(":");
                const ait = this.Now_ait.split(":");
                const now = this.clock.split(":");
                const yakusaTime = (yakusa[0] * 60) + parseInt(yakusa[1]);
                const aitTime = (ait[0] * 60) + parseInt(ait[1]);
                const nowTime = (now[0] * 60) + parseInt(now[1]);
                const yakusaUntill = yakusaTime - nowTime;
                const aitUntill = aitTime - nowTime;
                this.UntillBusYkusa = yakusaUntill + ""
                this.UntillBusAit = aitUntill + ""
            }
            // 
            if (this.Now_lecture != null) {
                if (this.Now_lecture.period == 0) {
                    this.UntilClass = ""
                }
                else {
                    const lecture = this.period2time(this.Now_lecture.period).split(":");
                    const now = this.clock.split(":");
                    const lectureTime = (lecture[0] * 60) + parseInt(lecture[1]);
                    const nowTime = (now[0] * 60) + parseInt(now[1]);
                    const lectureUntill = lectureTime - nowTime;
                    // 60
                    if (lectureUntill >= 60) {
                        const hour = Math.floor(lectureUntill / 60);
                        const minute = lectureUntill % 60;
                        this.UntilClass = hour + "" + minute + ""
                    } else {
                        this.UntilClass = lectureUntill + ""
                    }
                }
            }


            // 1001
            setTimeout(() => {
                this.tick();// tick
            }, 100);
        },
        BusData(now_yakusa, now_ait) {
            // console.log("BusData")
            this.Now_yakusa = now_yakusa
            this.Now_ait = now_ait
            if (this.Now_yakusa == null) {
                this.Now_yakusa = ""
            }
            if (this.Now_ait == null) {
                this.Now_ait = ""
            }
        },
        CarData(Food1, Food2) {
            // console.log("CarData")
            if (Food1 == null) {
                Food1 = ""
            }
            if (Food2 == null) {
                Food2 = ""
            }
            this.CarFood.push(Food1)
            this.CarFood.push(Food2)
        },
        lectureData: function () {
            const date = new Date();
            this.week = date.getDay();
            let timeH = date.getHours();
            let timeM = date.getMinutes();
            let time = timeH * 60 + timeM;
            if (this.week == 0 || this.week == 6) {
                this.week = 1;
                time = 0;
            }

            // 
            // time = 0;

            // 
            if (time <= 540) {
                time = 1;
            } else if (time <= 640) {
                time = 2;
            } else if (time <= 780) {
                time = 3;
            } else if (time <= 880) {
                time = 4;
            } else if (time <= 980) {
                time = 5;
            } else if (time <= 1080) {
                time = 6;
            } else {
                time = 0;
                this.Now_lecture = { "period": 0 }
                return;
            }
            for (let classData of this.TimeTables) {
                if (classData.youbi == this.weeks[this.week - 1]) {
                    while (this.Now_lecture == null) {
                        for (let classes of classData.classes) {
                            if (classes.period == time) {
                                this.Now_lecture = classes;
                                return;
                            }
                        }
                        time++;
                        if (time > 6) {
                            this.Now_lecture = { "period": 0 }
                            return;
                        }
                    }

                }
            }


            // console.log(time)
        },

        isDisplayxs: function () {
            const vuetify = usevuetify();
            return vuetify.display.xs;
        },
        getTextSizeClock: function () {
            if (this.isDisplayxs()) {
                return 'text-h6'
            } else {
                return 'text-h4'
            }
        },
        getIconSizeClock: function () {
            if (this.isDisplayxs()) {
                return 'small'
            } else {
                return 'x-large'
            }
        },
        getTextSizeTitle: function () {
            if (this.isDisplayxs()) {
                return 'text-body-2'
            } else {
                return 'text-h6'
            }
        },
        getTextSizeBody: function () {
            if (this.isDisplayxs()) {
                return 'text-caption'
            } else {
                return 'text-body-1'
            }
        },
    },

    created: function () {
        new Promise((resolve) => {
            this.TimeTables = this.timeTable;
            resolve();
        }).then(() => {
            this.lectureData();
        });
        this.AikodaiId = this.aikodaiId;
        this.tick();


    },
} 
</script>
