    methods: {

        getDate() { //  & 
            const weekItems = ["", "", "", "", "", "",""];
            this.date = new Date();
            this.week = this.date.getDay() - 1;
            if(this.week == -1) {
                this.week = 6;
            }
            this.dayOfWeek = weekItems[this.week];  
            this.now = this.date.getHours() + ":" + this.date.getMinutes() + ":" + this.date.getSeconds();
        },

        default() { // 
            this.nextLecture = '';
            this.nextRoom = '';
        },

        getNext() { // 
            const minutesOfLecture = [540,640,780,880,980,1080]; // ()
            const toDayLectures = this.TimeTables[this.week].classes; // 
            if(Object.keys(toDayLectures).length == 0) { // 
                this.default();
            } else {
                let minMinuDis = 100000000; // min minute distance
                let min = 0; // distanceindex
                for(let i = 0; i < Object.keys(toDayLectures).length; i++) { // 
                    const period = toDayLectures[i].period; // n
                    const minuDis = Math.abs(minutesOfLecture[period - 1] - (Number(this.date.getHours()) * 60 + Number(this.date.getMinutes())));

                    if(minMinuDis > minuDis) {
                        minMinuDis = minuDis;
                        min = i;
                    }
                }
                // console.log(toDayLectures[min].name);
                if(this.nextLecture != toDayLectures[min].name) { // 
                    this.nextLecture = toDayLectures[min].name;
                    this.nextRoom =  toDayLectures[min].locate;
                }
            }
        }, 

